const api = new ApiService();

const Star = (props: any) => {
  const {vote} = props;
  const items = [];
  for (let i = 1; i <= 5; i++) {
    items.push(
      <i key={i} className={`fa fa-star staricon ${i <= vote ? 'active': ''}`} aria-hidden="true" />
    );
  }
  return <>{items}</>;
}

interface PriceType {
  price: string;
  sales: string;
}

const Price = (props: PriceType) => {
  const before = parseFloat(props.price);
  const sale = parseFloat(props.sales);
  let display = (sale < 0 && props.sales.includes("%"))
    ? 
    <div className="price">
      <div className="price-original active">${props.price}</div>
      <div className="price-sale">${before*(1+sale/100)}</div>
    </div>
    : 
    <div className="price">
      <div className="price-original">${props.price}</div>
    </div>;
  return <>{display}</>;
}

