import axios from "axios";

export class ApiService {
  constructor () {
    axios.defaults.baseURL =
    'https://604b401bee7cb900176a1a9e.mockapi.io/api/v1/';
  }

  get(uri: (string|object)[], options = {}) {
    return new Promise((resolve, reject) => {
      axios.get(this.createURL(uri), {
        params: options,
      })
      .then((resp:any) => {
        resolve(resp.data);
      })
      .catch((err:any) => {
        reject(err);
      });
    });
  }

  public createURL(uri: (string|object)[]) {
    let paramsUrl:any;
    if (typeof uri[uri.length -1] !== 'string') {
      paramsUrl = uri.pop();
      let url = uri.join('/');
      Object.keys(paramsUrl).forEach(x => {
        url = url.replace(`:${x}` , paramsUrl[x]);
      });
      return url;
    } else {
      return uri.join('/');
    }
  }
}